import React from 'react';

class Dropdown extends React.Component {
    constructor(props){
        super(props);
        this.handleChange = this.handleChange.bind(this);
    }

    handleChange(event){
        var queries;
        var query_indices;
        if(event.target.value === 'MySQL'){
            query_indices = ['Most reviewed restaurants and their locations with a rating above 4', 'Most reviewed salons and their locations with a rating above 4', 'Most reviewed automotive shops and their locations with a rating above 4', 'Most reviewed education businesses and their locations with a rating above 4', 'Most reviewed health businesses and their locations with a rating above 4', 'Display the months when most amount of business takes place in a Pennsylvania', 'User who has most reviewed Starbucks'];
            queries = ['http://localhost:8080/mysql/mostReviewedBusiness/restaurant', 'http://localhost:8080/mysql/mostReviewedBusiness/salon', 'http://localhost:8080/mysql/mostReviewedBusiness/automotive', 'http://localhost:8080/mysql/mostReviewedBusiness/education', 'http://localhost:8080/mysql/mostReviewedBusiness/health', 'http://localhost:8080/mysql/mostBusinessMonths/PA', 'http://localhost:8080/mysql/userMostReviewed/Starbucks'];
        }
        else if(event.target.value === 'MongoDB'){
            query_indices = ['Get top rated business in a specific city', 'User who has tipped most', 'User who has most reviewed Starbucks'];
            queries = ['http://localhost:8080/mongo/bestBusiness?category=Restaurant&city=Pittsburgh', 'http://localhost:8080/mongo/userMostTipped?category=Restaurant&city=Pittsburgh','http://localhost:8080/mongo/userMostReviewed?businessName=Starbucks']
        }
        else if(event.target.value === 'Neo4j'){
            query_indices = ['User with most number of friends', 'Friends that like the same business', 'User who has most reviewed Starbucks'];
            queries = ['http://localhost:8080/neo4j/friendlyUsers', 'http://localhost:8080/neo4j/sameLiking?category=Breweries&userName=Stephanie', 'http://localhost:8080/neo4j/userMostReviewed?businessName=Starbucks']
        }
        this.props.onDropdownChange(event.target.value, query_indices, queries);
    }
    render() {
      
      return (
        <div>
            <label for="cars">Choose Database:</label>
            <select id="Databases" onChange={this.handleChange}>
            <option value="MySQL">MySQL</option>
            <option value="MongoDB">MongoDB</option>
            <option value="Neo4j">Neo4j</option>
            </select>
        </div>
        
        );
    }   
    
}
  
export default Dropdown;