import React from 'react';
import TableRow from './TableRow';
import Table1 from "react-bootstrap/Table";
import Button from "react-bootstrap/Button";

class Table extends React.Component{
    
    render() {
        const header = Object.keys(this.props.review[0]);
        const header_print = [];
        header.map((value) => {
            header_print.push(<td> {value} </td>);
        }) 
        const rows = [];
        this.props.review.map((element) => {
            rows.push(<TableRow  row = {element} keys = {header}/>);
        })
        const head =  [];
        header.map((key) => {
            head.push(<td>{key}</td>);
        })
        return (
            <div>
            <Table1 striped bordered hover>
                
                    <thead>
                        {header_print}  
                    </thead>
                    <tbody>
                        {rows}
                    </tbody>
                
            
            </Table1>
            <Button href="/">Home</Button>
            </div>
        );
    }
}


export default Table;