import React from 'react';
import Button from "react-bootstrap/Button";
import './custom.scss';

class Query extends React.Component {
    constructor(props){
        super(props);
        this.handleQuerySelection = this.handleQuerySelection.bind(this);
        this.state = {   
            selectedQuery : '',
            selectedQuery_index : ''
        };
    }

    handleQuerySelection(event){
        this.setState(
            {
                selectedQuery : event.target.name
            }
        )
        this.props.onQuerySelection(event.target.id);
    }

    
    
    render() {
        // 
        const list = [];             
        this.props.query_nums.forEach((q,i) => {
        list.push(<Button name={q} id={i} onClick = {this.handleQuerySelection} variant="warning" block>{q}</Button>);
        list.push(<br></br>);         
        });

        
        if(this.props.showQueryList){
            return (
                <div>
                {list}
                </div>
            );
        }
        else{
            return (
                <div>
                    {this.state.selectedQuery}
                </div>
            );
        }

    }
}

export default Query;